"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"

export default function MergePDF() {
  const [files, setFiles] = useState<File[]>([])
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")
  const router = useRouter()

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const fileList = Array.from(e.target.files)
      // Only accept PDF files
      const pdfFiles = fileList.filter(file => file.type === "application/pdf")
      if (pdfFiles.length !== fileList.length) {
        setError("Please upload only PDF files")
        return
      }
      setFiles(pdfFiles)
      setError("")
    }
  }

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault()
    if (e.dataTransfer.files) {
      const fileList = Array.from(e.dataTransfer.files)
      // Only accept PDF files
      const pdfFiles = fileList.filter(file => file.type === "application/pdf")
      if (pdfFiles.length !== fileList.length) {
        setError("Please upload only PDF files")
        return
      }
      setFiles(pdfFiles)
      setError("")
    }
  }

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault()
  }

  const handleMerge = async () => {
    if (files.length < 2) {
      setError("Please upload at least 2 PDF files to merge")
      return
    }

    try {
      setLoading(true)
      setError("")

      const formData = new FormData()
      files.forEach(file => {
        formData.append("files", file)
      })

      const response = await fetch("/api/merge", {
        method: "POST",
        body: formData
      })

      if (!response.ok) {
        throw new Error("Failed to merge PDF files")
      }

      // Get the blob from the response
      const blob = await response.blob()
      
      // Create a download link
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement("a")
      a.href = url
      a.download = "merged.pdf"
      document.body.appendChild(a)
      a.click()
      window.URL.revokeObjectURL(url)
      document.body.removeChild(a)

      setFiles([])
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">Merge PDF Files</h1>
        <p className="text-lg text-gray-600">
          Combine PDFs in the order you want with the easiest PDF merger available.
        </p>
      </div>

      <div className="max-w-3xl mx-auto">
        <div 
          className="bg-white rounded-lg shadow-sm border-2 border-dashed border-gray-300 p-12"
          onDrop={handleDrop}
          onDragOver={handleDragOver}
        >
          <div className="text-center">
            <svg
              className="mx-auto h-12 w-12 text-gray-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 13h6m-3-3v6m5 5H7a2 2 0 01-2-2V7a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
              />
            </svg>
            <div className="mt-4">
              <label htmlFor="file-upload" className="cursor-pointer">
                <span className="mt-2 block text-sm font-medium text-gray-900">
                  Drop PDF files here
                </span>
                <span className="mt-1 block text-sm text-gray-500">
                  or click to select files
                </span>
                <input
                  id="file-upload"
                  name="file-upload"
                  type="file"
                  className="hidden"
                  multiple
                  accept=".pdf"
                  onChange={handleFileChange}
                />
              </label>
            </div>
          </div>

          {error && (
            <div className="mt-4 p-4 bg-red-50 rounded-md">
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}

          {files.length > 0 && (
            <div className="mt-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4">Selected Files:</h3>
              <ul className="space-y-2">
                {files.map((file, index) => (
                  <li key={index} className="flex items-center justify-between py-2 px-4 bg-gray-50 rounded">
                    <span className="text-sm text-gray-900">{file.name}</span>
                    <span className="text-sm text-gray-500">{(file.size / 1024 / 1024).toFixed(2)} MB</span>
                  </li>
                ))}
              </ul>
              <button
                type="button"
                className={`mt-6 w-full inline-flex justify-center items-center px-6 py-3 border border-transparent text-base font-medium rounded-md text-white ${
                  loading ? "bg-blue-400 cursor-not-allowed" : "bg-blue-600 hover:bg-blue-700"
                }`}
                onClick={handleMerge}
                disabled={loading}
              >
                {loading ? "Merging..." : "Merge PDF Files"}
              </button>
            </div>
          )}
        </div>

        <div className="mt-8 text-center">
          <h2 className="text-lg font-medium text-gray-900 mb-4">How to Merge PDF Files</h2>
          <ol className="text-left space-y-4">
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">1</span>
              <span className="text-gray-600">Upload your PDF files by dropping them in the upload area or clicking to browse</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">2</span>
              <span className="text-gray-600">Arrange the files in the order you want them merged</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">3</span>
              <span className="text-gray-600">Click "Merge PDF Files" and download your combined PDF</span>
            </li>
          </ol>
        </div>
      </div>
    </div>
  )
}
